.main-ias-community-div {
  padding: 90px 0px;
}

@media (max-width: 1024px) {
  .main-ias-community-div {
    padding: 60px 0px;
  }
}

@media (max-width: 768px) {
  .main-ias-community-div {
    padding: 40px 0px;
  }
}

@media (max-width: 480px) {
  .main-ias-community-div {
    padding: 30px 0px;
  }
}

.component-heading-div h4 {
  color: #211b3a;
  font-family: Urbanist;
  font-size: 12px;
  font-weight: 400;
}

@media (max-width: 768px) {
  .component-heading-div h4 {
    font-size: 11px;
  }
}

@media (max-width: 480px) {
  .component-heading-div h4 {
    font-size: 10px;
    text-align: center;
  }
}

.head-button {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.head-button h1 {
  color: #211b3a;
  font-family: Urbanist;
  font-size: 40px;
  font-weight: 600;
  margin: 0px;
}

@media (max-width: 768px) {
  .head-button h1 {
    font-size: 28px;
    margin-bottom: 10px;
  }
}

@media (max-width: 480px) {
  .head-button h1 {
    font-size: 22px;
    text-align: center;
    width: 100%;
  }
}

.btn-mem {
  border-radius: 4px;
  border: 1px solid #000;
  opacity: 0.71;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 500;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

@media (max-width: 768px) {
  .btn-mem {
    font-size: 14px;
    padding: 10px 20px;
  }
}

@media (max-width: 480px) {
  .btn-mem {
    width: 100%;
    justify-content: center;
  }
}

.left-name-designation-div {
  width: 60%;
  overflow-y: auto;
}

@media (max-width: 1024px) {
  .left-name-designation-div {
    order: 2 !important;
    width: 100% !important;
    margin-top: 20px;
    max-height: 220px;
    overflow-y: auto;
    scrollbar-width: thin;
  }
}

@media (max-width: 480px) {
  .left-name-designation-div {
    max-height: 180px;
  }
}

.right-image-div {
  width: 40%;
  justify-content: flex-end;
}

@media (max-width: 1024px) {
  .right-image-div {
    order: 1;
    width: 100% !important;
    justify-content: center !important;
  }
}

.name-image {
  margin-top: 62px;
  height: 440px;
  overflow: hidden;
}

@media (max-width: 768px) {
  .name-image {
    flex-direction: column;
    margin-top: 40px;
    height: auto;
    align-items: center;
    gap: 20px;
  }
}

@media (max-width: 480px) {
  .name-image {
    margin-top: 20px;
    gap: 15px;
  }
}

.innerdiv {
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
}

.innerdiv h4 {
  color: #211b3a;
  font-family: Urbanist;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  width: 20%;
}

@media (max-width: 768px) {
  .innerdiv h4 {
    width: 100%;
    font-size: 13px;
    margin-bottom: 5px;
    text-align: center;
  }
}

.innerdiv h1 {
  font-family: Urbanist;
  font-size: 42px;
  font-weight: 500;
  text-transform: uppercase;
  color: #b8b6bf;
  width: 70%;
  cursor: pointer;
  transition: all 0.5s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

@media (max-width: 1400px) {
  .innerdiv h1 {
    font-size: 40px;
  }
}

@media (max-width: 768px) {
  .innerdiv h1 {
    font-size: 28px;
    width: 100%;
    text-align: center;
  }
}

@media (max-width: 480px) {
  .innerdiv h1 {
    font-size: 22px;
  }
}

.innerdiv h1.active {
  font-weight: 600;
  background: linear-gradient(
    130deg,
    #492fb2 -1.74%,
    rgba(73, 47, 178, 0.61) 125.81%
  );
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.imagecontact-div {
  max-width: 320px;
  width: 100%;
}

.imagecontact-div img {
  width: 100%;
  height: auto;
  border-radius: 6px;
}

@media (max-width: 480px) {
  .imagecontact-div {
    max-width: 100%;
  }
}

.email-cont-div {
  border-radius: 0 0 4px 4px;
  background: #e3e3e3;
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
  gap: 10px;
}

.email-cont-div .linkdln {
  color: #000;
  font-family: Urbanist;
  font-size: 15px;
  font-weight: 400;
  display: flex;
  align-items: center;
  gap: 3px;
  cursor: pointer;
  flex: 1 1 45%;
  justify-content: center;
}

@media (max-width: 768px) {
  .email-cont-div .linkdln {
    font-size: 14px;
  }
}

@media (max-width: 480px) {
  .email-cont-div .linkdln {
    font-size: 13px;
  }
}

.btn-connect {
  border-radius: 4px;
  border: 1px solid #5d48b3;
  background: #5d48b3;
  padding: 12px 24px;
  color: #fff;
  font-family: Urbanist;
  font-size: 16px;
  font-weight: 600;
  letter-spacing: 0.15px;
  width: 100%;
}

@media (max-width: 768px) {
  .btn-connect {
    font-size: 14px;
    padding: 10px 20px;
  }
}

@media (max-width: 480px) {
  .btn-connect {
    width: 100%;
  }
}

.arrow-btn {
  background: none;
  border: none;
  font-size: 20px;
  cursor: pointer;
  padding: 8px 12px;
  border-radius: 39px;
  border: 0.5px solid #b3b3b3;
  transition: color 0.2s;
}

.arrow-btn:hover:not(:disabled) {
  color: #5d48b3;
}

.arrow-btn:disabled {
  opacity: 0.3;
  cursor: not-allowed;
}

@media (max-width: 480px) {
  .arrow-btn {
    padding: 5px 8px;
    font-size: 16px;
  }
}
